import React, { useState, useContext, useEffect } from "react";
import { UserContext } from "../../context/UserContext";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import axios from "axios";
import { getValidationErrors } from "../../utils/FormValidator";
import { CheckLoggedinUser } from "../../utils/CheckLoggedinUser";
import { Switch, Route, Redirect } from "react-router-dom";

const Login = () => {
  const classes = useStyles();
  const darkTheme = createMuiTheme({
    palette: {
      type: "dark",
      primary: {
        main: "#fff",
      },
      secondary: {
        main: "#e57373",
      },
    },
    typography: {
      fontFamily: "ubuntu",
      fontSize: 18,
    },
  });

  const defaultError = {
    email: "",
    password: "",
  };
  const url = `${process.env.REACT_APP_API_URL}/login`;

  const [loading, setLoading] = useState(true);
  const [user, setUser] = useContext(UserContext);
  const [credential, setCredential] = useState({ email: "", password: "" });
  const [message, setMessage] = useState(false);
  const [error, setError] = useState(defaultError);

  useEffect(() => {
    CheckLoggedinUser(setUser);
    setLoading(false);
    // eslint-disable-next-line
  }, []);

  const attemptLogin = async (e) => {
    e.preventDefault();
    let res = await axios.post(url, credential);
    const { message, status, errors, data } = res.data;

    if (!status) {
      setMessage(message);
      if (errors) {
        getValidationErrors(errors, setError);
      }
      return false;
    }

    setUser(data);
    localStorage.setItem("budash", JSON.stringify(data));
    window.location.href = `${process.env.REACT_APP_PUBLIC_URL}/dashboard`;
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (user !== null) {
    return (
      <Switch>
        <Route path="/" render={() => <Redirect to="/dashboard"></Redirect>} />
      </Switch>
    );
  }

  return (
    <ThemeProvider theme={darkTheme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <form
            className={classes.form}
            noValidate
            onSubmit={attemptLogin}
            autoComplete="off"
          >
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              error={error.email ? true : false}
              helperText={error.email ?? ""}
              value={credential.email}
              onChange={(e) =>
                setCredential({ ...credential, email: e.target.value })
              }
              autoComplete="email"
              autoFocus
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              error={error.password ? true : false}
              helperText={error.password ?? ""}
              id="password"
              value={credential.password}
              onChange={(e) =>
                setCredential({ ...credential, password: e.target.value })
              }
              autoComplete="current-password"
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Sign In
            </Button>

            {/*TODO : add style*/}
            {message ? <Typography color="error">{message}</Typography> : ""}

            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
        <Box mt={8}>
          <Copyright />
        </Box>
      </Container>
    </ThemeProvider>
  );
};

export default Login;

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "} {new Date().getFullYear()}{" "}
      <Link color="inherit" href="https://budash.net/">
        Budash.net
      </Link>
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));
